<template>
  <div>
    <input type="text" placeholder="输入商品名称" class="title">
    <textarea placeholder="请描述物品" class="text"></textarea>
    <div>
      <van-uploader v-model="fileList" :max-size="500 * 1024" :after-read="afterRead" :before-delete="beforeDelete" max-count="6" />
    </div>
    <div class="fenlei">
      <form>
        <select size="3" class="select">
          <option v-for="item in results" :key="item.id" :data-id="item.id">{{ item.name }}</option>
        </select>
        <div class="categroy">选择商品分类</div>
      </form>
    </div>
    <div class="pricediv" @click="keyboardToggle">价格
      <div class="arrow_right">&gt;</div>
      <div class="price">￥0</div>
    </div>
    <van-number-keyboard :show="show" :title="value" extra-key="." close-button-text="完成" v-model="value"
      @blur="loseFocus" @input="onInput" @delete="onDelete" />
    <button @click="submitGoods" class="button">发布</button>
  </div>
</template>

<script>
import { uploadPhotos } from '@/api/uploadphoto'
import { postGoodsInfo } from '@/api/postgoodsinfo'
import { getSelect } from '@/api/getcategroy'

export default {
  name: 'xianZhi',
  data () {
    return {
      fileList: [
      ],
      // files: [], // 保存图片文件和图片文件id
      // filesID: [], // 图片文件id
      show: false,
      filesArr: [], // 保存后端传过来的图片信息
      filesImgUrl: [], // 保存最后发送给后端的所有图片路劲
      value: '',
      results: [],
      count: 0
    }
  },
  async created () {
    if (this.$store.getters.token) {
      const { results } = await getSelect()
      this.results = results
    }
  },
  updated () {
    document.querySelector('.select').options[0].selected = true
  },
  methods: {
    async submitGoods () {
      this.filesImgUrl = []
      for (let i = 0; i < this.filesArr.length; i++) {
        this.filesImgUrl.push(this.filesArr[i].url)
      }
      console.log(this.filesImgUrl)
      for (let i = 0; i < this.filesImgUrl.length; i++) {
        // 处理图片路径
        this.filesImgUrl[i] = this.filesImgUrl[i].substring(6)
        this.filesImgUrl[i] = 'http://127.0.0.1:3008/api/images/' + this.filesImgUrl[i]
      }
      const describe = document.querySelector('.text').value
      const categroy1 = document.querySelector('.select').value
      const mySelect = document.querySelector('.select')
      const title = document.querySelector('.title').value
      const index = mySelect.selectedIndex
      const id = parseInt(mySelect.options[index].dataset.id)
      const price = parseFloat(document.querySelector('.price').innerHTML.substring(1))
      const res = await postGoodsInfo(describe, this.filesImgUrl, categroy1, price, id, title)
      if (res.status === 0) {
        this.$toast(res.message)
        this.$router.go(0)
      }
    },
    async afterRead (file) {
      if (this.count < 10) {
        file.status = 'uploading'
        file.message = '上传中...'
        const formData = new FormData()
        formData.append('file', file.file)
        const res = await uploadPhotos(formData)
        res.file = file
        // console.log(res)
        this.filesArr.push(res)
        // console.log(this.filesArr)
        // 暂时用不上
        // this.files.push({
        //   id: res.id,
        //   file: file
        // })
        // this.filesID.push(res.id)
        // console.log(this.fileList)
        // console.log(this.filesID)
        if (res.status === 0) {
          file.status = 'done'
          file.message = '上传成功'
        } else {
          file.status = 'failed'
          file.message = '上传失败'
        }
        this.count++
      } else {
        this.$toast('您上传图片次数太多啦')
      }
    },
    beforeDelete (file) {
      // this.files = this.files.find(obj => obj.file === file)
      // this.fileList = this.fileList.filter(obj =>obj.)
      // const OBJ = this.filesArr.find(obj => obj.file === file)
      this.filesArr = this.filesArr.filter(obj => obj.file !== file)
      console.log(this.filesArr.length)
      this.fileList = this.fileList.filter(obj => obj.file !== file.file)
    },
    onInput (value) {
      this.$toast(value)
    },
    onDelete () {
      this.$toast('删除')
    },
    keyboardToggle () {
      this.show = true
      if (this.value === '0') {
        this.value = ''
      }
    },
    loseFocus () {
      this.show = false
      this.testPrice()
      document.querySelector('.price').innerHTML = '￥' + this.value
    },
    testPrice () {
      const reg = /(^[1-9]\d*(\.\d{1,2})?$)|(^0(\.\d{1,2})?$)/
      const result = reg.test(this.value)
      if (!result) {
        this.value = '0'
        this.$toast('请输入正确的价格')
      }
    }
  }
}

</script>

<style scoped>
  .text{
    width: 100%;
    height: 250px;
  }
  .fenlei{
    height:80px ;
  }
  .categroy{
    height: 80px;
    float: left;
    color: #524f4f;
    line-height: 80px;
  }
  .select{
    height: 80px;
    width: 200px;
    text-align: center;
    float: left;
  }
  .pricediv{
    height: 30px;
    border-radius: 5px;
    border: 1px solid #aeabab;
    padding-left: 25px;
    line-height: 30px;
  }
  .price{
    height: 30px;
    float: right;
    margin-right: 15px;
    color: red;
  }
  .arrow_right{
    float: right;
    margin-right: 5px;
  }
  .button{
    background-color: rgb(250, 87, 87);
    width: 100px;
    height: 30px;
    display: block;
    margin: 0 auto;
  }
  .title{
    width: 100%;
  }
</style>
