import { getInfo, setInfo } from '@/utils/storage'

export default {
  nameSpaced: true,
  state () {
    return {
      userInfo: getInfo()
    }
  },
  mutations: {
    getUserInfo (state, info) {
      state.userInfo = info
      setInfo(info)
    }
  },
  getters: {
  },
  actions: {
  }
}
