<template>
  <div>
    <van-nav-bar fixed title="添加管理员" left-arrow @click-left="$router.go(-1)" class="bar" />
    <div v-show="status === false">
      <div class="main">
        <div class="username"><input v-model="username" type="text" placeholder="请输入账号"></div>
        <div class="password"><input v-model="firstpwd" type="password" placeholder="请输入密码"></div>
        <div class="password"><input v-model="secondpwd" type="password" placeholder="请再次输入密码"></div>
      </div>
      <div class="booter">
        <input type="button" value="增加" @click="fun">
      </div>
    </div>
    <div v-show="status === true" class="big">
      <div v-for="item in result" :key="item.id" class="bigdiv"><span v-show="item.status != 0" class="span">账户: {{ item.username }}</span>
        <button v-if="item.status === 1" class="del" @click="stop(item.id)">停用该分类</button>
        <button v-if="item.status === 2" class="del" @click="start(item.id)">启用该分类</button>
      </div>
    </div>
    <div class="stop" @click="status = !status">禁用账户</div>
  </div>
</template>

<script>
import { zhuce, stopAdmin, startAdmin, getAdminMsg } from '@/api/adminzhuce'
export default {
  name: 'addAdminPage',
  data () {
    return {
      username: '',
      name: '',
      firstpwd: '',
      secondpwd: '',
      status: false,
      result: []
    }
  },
  methods: {
    async fun () {
      if (this.username.length < 6 || this.username.length > 18) {
        this.$toast('账号长不能小于6或大于18')
        return
      }
      if (this.firstpwd.length < 6 || this.firstpwd.length > 18) {
        this.$toast('密码不能小于6或大于18')
        return
      }
      if (this.secondpwd !== this.firstpwd) {
        this.$toast('两次密码不一致')
        return
      }
      console.log(this.name)
      const { message } = await zhuce(this.username, this.firstpwd, this.name)
      this.$toast(message)
    },
    async getAdminMsg () {
      const res = await getAdminMsg()
      this.result = res.results
      console.log(res)
    },
    async stop (id) {
      const res = await stopAdmin(id)
      console.log(res)
      this.getAdminMsg()
    },
    async start (id) {
      const res = await startAdmin(id)
      console.log(res)
      this.getAdminMsg()
    }
  },
  created () {
    this.getAdminMsg()
  }

}
</script>

<style scoped>
.bigdiv{
  border-bottom: 1px black solid;
}
.big{
  margin-top: 70px;
  word-wrap: break-word;
}
.span{
  display: inline-block;
  width: 250px;
}
.stop{
      width: 100px;
      height: 100px;
      line-height: 100px;
      text-align: center;
      position: fixed;
      right: 10px;
      bottom: 10px;
      border-radius: 50%;
      background-color: rgb(62, 174, 248);
}
.del {
  width: 20%;
  background-color: red;
  background-color: rgb(248, 62, 62);
}
.main {
  height: 350px;
  width: 250px;
  margin: auto;
  margin-top: 50px;
}

.username {
  width: 250px;
  height: 50px;
  margin: auto;
  margin-bottom: 50px;
  background-color: white;
}

.username input {
  width: 100%;
  height: 100%;
}

.password {
  width: 250px;
  height: 50px;
  margin: auto;
  margin-bottom: 50px;
  background-color: white;
}

.password input {
  width: 100%;
  height: 100%;
}

.booter {
  height: 100px;
  width: 250px;
  margin: auto;
}

.booter input {
  height: 50px;
  width: 150px;
  margin-left: 50px;
  background-color: rgb(233, 122, 146);
  color: #fff;
}
.del{
  background-color: antiquewhite;
}
</style>
