<template>
<div>
    支付了
</div>
</template>

<script>
import { queryOrder, payOrder } from '@/api/buygoods'
// import { mapState } from 'vuex'
import store from '@/store/index'
export default {
  name: 'payPage',
  data () {
    return {
      id1: '',
      id2: ''
    }
  },
  async created () {
    this.id1 = this.$route.query.out_trade_no
    this.id2 = this.$route.query.trade_no
    const res3 = await queryOrder(this.id1, this.id2)
    if (res3.result.statusa === 2 || res3.result.statusa === 3) {
      this.$toast('支付成功')
      const msg = store.getters.order
      console.log(msg)
      await payOrder(msg)
    } else {
      this.$toast('支付失败')
    }
    this.$router.push('/tuijian')
  }
}
</script>

