<template>
  <div class="div">
    <van-nav-bar fixed title="新闻详情页" left-arrow @click-left="$router.go(-1)" class="bar" />
    <h4 v-if="newsMsg[0]">{{ newsMsg[0].title }}</h4>
    <p class="time" v-if="newsMsg[0]">{{ this.newsMsg[0].create_time}}</p>
    <p v-for="item in text" :key="item">&nbsp;&nbsp;{{ item }}</p>
  </div>
</template>

<script>
import { getNewsById } from '@/api/getnews'
export default {
  name: 'newsProdetail',
  data () {
    return {
      newsMsg: [],
      text: ''
    }
  },
  computed: {
    newsId () {
      return this.$route.params.id
    }
  },
  methods: {
    async getdetail () {
      const res = await getNewsById(this.newsId)
      this.newsMsg = res.message
      this.text = this.newsMsg[0].content.split('br')
      console.log(this.text)
      this.newsMsg[0].create_time = this.format(parseInt(this.newsMsg[0].create_time))
    },
    format (dataString) {
      console.log(dataString)
      const time = new Date(dataString)
      const year = time.getFullYear()
      console.log(year)
      const month = time.getMonth() + 1
      const day = time.getDate()
      const hour = time.getHours()
      const minute = time.getMinutes()
      const second = time.getSeconds()
      return year + '-' + (month < 10 ? '0' + month : month) + '-' + (day < 10 ? '0' + day : day) + ' ' + (hour < 10 ? '0' + hour : hour) + ':' + (minute < 10 ? '0' + minute : minute) + ':' + (second < 10 ? '0' + second : second)
    }
  },
  created () {
    this.getdetail()
  }

}
</script>

<style scoped>
.div{
  padding-top: 48px;
}
h4{
  text-align: center;
  font-size: 30px;
}
.time{
  font-size: 12px;
  text-align: center;
}
</style>
