<template>
  <div>
    <van-nav-bar title="登录" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <div class="main">
      <div class="username"><input v-model="username" type="text" placeholder="请输入账号"></div>
      <div class="password"><input v-model="password" type="password" placeholder="请输入密码"></div>
      <input type="radio" name="sex" value="user" checked class="admin1">普通用户
      <input type="radio" name="sex" value="admin" class="admin1">管理员
      <div class="zhuce"><a href="javaScript:;" @click="zhuce">注册</a></div>
    </div>
    <div class="booter">
      <input type="button" value="登录" @click="fun">
    </div>
  </div>
</template>

<script>
import { login } from '@/api/login'
export default {
  name: 'loginIndex',
  data () {
    return {
      username: '',
      password: '',
      value: ''
    }
  },
  methods: {
    zhuce () {
      this.$router.push('/zhuce')
    },
    changepwd () {
      this.$router.push('/changepwd')
    },
    async fun () {
      if (this.username.length < 6 || this.username.length > 18) {
        this.$toast('账号长不能小于6或大于18')
        return
      }
      if (this.password.length < 6 || this.password.length > 18) {
        this.$toast('密码不能小于6或大于18')
        return
      }
      const ipt = document.querySelectorAll('input')
      console.log(ipt)
      if (ipt[2].checked === true) {
        this.value = 'user'
      } else {
        this.value = 'admin'
      }
      const res = await login(this.username, this.password, this.value)
      const obj = { token: res.token, userId: '', role: res.role }
      this.$store.commit('getUserInfo', obj)
      this.$toast('登录成功')
      const url = this.$route.query.backUrl || '/'
      setTimeout(() => {
        this.$router.replace(url)
      }, 1000)
    }
  }
  // created () {
  //   const role = this.$route.query.role || 0
  //   console.log(role)
  //   if (role === '1') {
  //     console.log(document.querySelectorAll('.admin1')[0].checked)
