<script>
import { getUrlCode, getLogin, loginFn } from '@/api/login'

export default {
  name: 'LoginPage',
  data () {
    return {
      picUrl: '', // 通过请求获取到的图片路径
      keyCode: '', // 获取的图片验证key
      iptCode: '', // 输入框中的验证码
      totalSecond: 60,
      second: 60,
      timer: null,
      telnumber: '',
      msgCode: ''
    }
  },
  created () {
    this.getPicData()
  },
  destroyed () {
    clearInterval(this.timer)
  },
  methods: {
    async getPicData () {
      const { data: { key, base64 } } = await getUrlCode()
      this.picUrl = base64
      this.keyCode = key
    },
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.telnumber)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.iptCode)) {
        this.$toast('请输入正确的验证码')
        return false
      }
      return true
    },
    async startSecond () {
      const that = this
      if (!that.validFn()) {
        return
      }
      const { status, message } = await getLogin(that.telnumber, that.iptCode, that.keyCode)
      if (status === 500) {
        this.$toast(message)
        return
      }
      this.$toast('请求发送成功')
      if (that.timer || that.totalSecond !== this.second) {
        clearInterval(that.timer)
      }
      that.timer = setInterval(function () {
        that.second--
        if (that.second <= 0) {
          clearInterval(that.timer)
          that.second = that.totalSecond
        }
      }, 1000)
    },
    async dlLogin () {
      if (!this.validFn()) {
        return
      }
      if (!/^\d{6}$/.test(this.msgCode)) {
        this.$toast('请输入正确的验证码')
        return
      }
      const res = await loginFn(this.telnumber, this.msgCode)
      this.$store.commit('getUserInfo', res.data)
      this.$router.push('/')
      this.$toast('登录成功')
    }
  }
}
</script>

