<template>
  <div>
    <NewsItem :news="newsMsg"></NewsItem>
  </div>
</template>

<script>
import { getNews } from '@/api/getnews'
import NewsItem from '@/components/NewsItem.vue'
export default {
  name: 'newsIndex',
  components: {
    NewsItem
  },
  data () {
    return {
      newsMsg: []
    }
  },
  async created () {
    const res = await getNews()
    console.log(res)
    this.newsMsg = res.message
  }
}
</script>

