import request from '@/utils/request'
import store from '@/store/index'

export const zhuce = function (username, password) {
  return request.post('/admin/adminzhuce', {
    username,
    password
  }, {
    headers: {
      Authorization: store.getters.token
    }
  }
  )
}
export const getAdminMsg = function () {
  return request.get('/admin/getadminmsg',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
export const stopAdmin = function (id) {
  return request.post('/admin/stopadmin', {
    id
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}
export const startAdmin = function (id) {
  return request.post('/admin/startadmin', {
    id
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}
