<template>
  <div>
    <van-nav-bar title="修改密码" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <div class="main">
      <div class="username"><input v-model="username" type="text" placeholder="请输入账号"></div>
      <div class="password"><input v-model="oldpwd" type="password" placeholder="请输入旧密码"></div>
      <div class="new_password"><input v-model="newpwd" type="password" placeholder="输入新密码"></div>
      <div v-show="mbname">
        <div class="mbname">请输入密保内容:{{ mbname }}</div>
        <div class="password"><input v-model="mbword" type="text" placeholder="输入密保"></div>
      </div>
    </div>
    <div class="booter">
      <input type="button" value="确定修改" @click="fun1">
    </div>
  </div>
</template>

<script>
import { change } from '@/api/changepwd'
import { getUserInfo } from '@/api/getuserinfo'
export default {
  name: 'changeIndex',
  data () {
    return {
      username: '',
      oldpwd: '',
      newpwd: '',
      mbword: '',
      mbname: ''
    }
  },
  methods: {
    async fun1 () {
      if (this.username.length < 6 || this.username.length > 18) {
        this.$toast('账号长不能小于6或大于18')
        return
      }
      if (this.oldpwd.length < 6 || this.oldpwd.length > 18) {
        this.$toast('旧密码不能小于6或大于18')
        return
      }
      if (this.newpwd.length < 6 || this.newpwd.length > 18) {
        this.$toast('新密码不能小于6或大于18')
        return
      }
      if (!this.$store.getters.token) {
        return
      }
      const res = await change(this.username, this.oldpwd, this.newpwd, this.mbword)
      console.log(res)
      this.$toast('修改成功')
      this.$router.push('/login')
    },
    async fun2 () {
      const res = await getUserInfo()
      this.mbname = res.results.mbname
    }
  },
  created () {
    this.fun2()
  }

}
</script>

<style scoped>
.header {
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: rgb(233, 122, 146);
    margin-bottom: 60px;
}
.mbname{
    margin-top: 25px;
    height: 40px;
    text-align: left;
}
.main {
    margin-top: 50px;
    height: 370px;
    width: 250px;
    margin: auto;
    margin: 50px;
}

.username {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 20px;
    background-color: white;
}

.username input {
    width: 100%;
    height: 100%;
}

.password {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 20px;
    background-color: white;
}

.password input {
    width: 100%;
    height: 100%;
}
.new_password {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 50px;
    background-color: white;
}

.new_password input {
    width: 100%;
    height: 100%;
}

.booter {
    height: 100px;
    width: 250px;
    margin: auto;
}

.booter input {
    height: 50px;
    width: 150px;
    margin-left: 50px;
    background-color: rgb(233, 122, 146);
    color: #fff;
}
</style>
