import Vue from 'vue'
import VueRouter from 'vue-router'
import layout from '@/views/layout'
import login from '@/views/login'
import myorder from '@/views/myorder'
import pay from '@/views/pay'
import prodetail from '@/views/prodetail'
import search from '@/views/search/index'
import list from '@/views/search/list'
import cart from '@/views/layout/cart'
import category from '@/views/layout/category'
import home from '@/views/layout/home'
import user from '@/views/layout/user'
import store from '@/store/index'
import changepwd from '@/views/changepasswd'
import zhuce from '@/views/zhuce'
import xianzhi from '@/views/layout/xianzhi'
import tuijian from '@/views/layout/tuijian'
import news from '@/views/layout/news'
import usermsg from '@/views/layout/usermsg.vue'
import updatauserinfo from '@/views/updatauserinfo/updatauserinfo.vue'
import admin from '@/views/admin/index.vue'
import submitnew from '@/views/admin/submitnew.vue'
import addadmin from '@/views/admin/addadmin.vue'
import addcategroy from '@/views/admin/addcategroy.vue'
import comfirm from '@/views/admin/comfirm.vue'
import comfirmpage from '@/views/comfirmpage/index.vue'
import newsprodetail from '@/views/newsprodetail/prodetail.vue'
import { Dialog, Toast } from 'vant'
import paypage from '@/views/pay/pay.vue'

Vue.use(VueRouter)

const router = new VueRouter({
  routes: [
    {
      path: '/',
      component: layout,
      redirect: '/home',
      children: [
        { path: '/cart', component: cart },
        { path: '/category', component: category },
        {
          path: '/home',
          component: home,
          redirect: 'tuijian',
          children: [
            { path: '/tuijian', component: tuijian },
            { path: '/news', component: news }
          ]
        },
        { path: '/user', component: user },
        { path: '/xianzhi', component: xianzhi }
      ]
    },
    {
      path: '/admin',
      component: admin,
      redirect: '/comfirm',
      children: [
        { path: '/comfirm', component: comfirm },
        { path: '/submitnew', component: submitnew },
        { path: '/addadmin', component: addadmin },
        { path: '/addcategroy', component: addcategroy }
      ]
    },
    { path: '/login', component: login },
    { path: '/myorder', component: myorder },
    { path: '/pay', component: pay },
    { path: '/prodetail/:id', component: prodetail },
    { path: '/newsprodetail/:id', component: newsprodetail },
    { path: '/search', component: search },
    { path: '/list', component: list },
    { path: '/changepwd', component: changepwd },
    { path: '/zhuce', component: zhuce },
    { path: '/usermsg', component: usermsg },
    { path: '/updatauserinfo', component: updatauserinfo },
    { path: '/comfirmpage/:id', component: comfirmpage },
    { path: '/paypage', component: paypage }
  ]
})

const authUrl = ['/pay', '/xianzhi']
const adminUrl = ['/admin', '/comfirm', '/submitnew', '/addcategroy']
const superAdminurl = ['/addadmin']
router.beforeEach((to, from, next) => {
  const token = store.getters.token
  const role = store.getters.role
  if (!authUrl.includes(to.path) && !adminUrl.includes(to.path) && !superAdminurl.includes(to.path)) {
    next()
    return
  }
  if (adminUrl.includes(to.path) && !superAdminurl.includes(to.path)) {
    if (token && role !== 0) {
      next()
    } else {
      Dialog.confirm({
        title: '温馨提示',
        message: '此时需要先登录管理员账号才能继续操作哦',
        confirmButtonText: '去登录',
        cancelButtonText: '再逛逛'
      })
        .then(() => {
          router.push({
            path: '/login',
            query: {
              role: 1
            }
          })
        })
        .catch(() => { })
    }
  } else if (superAdminurl.includes(to.path)) {
    if (token && role === 2) {
      next()
    } else {
      Toast('你没有权限')
    }
  }
  if (authUrl.includes(to.path)) {
    if (token && role === 0) {
      next()
    } else {
      Dialog.confirm({
        title: '温馨提示',
        message: '此时需要先登录才能继续操作哦',
        confirmButtonText: '去登录',
        cancelButtonText: '再逛逛'
      })
        .then(() => {
          router.push({
            path: '/login'
          })
        })
        .catch(() => { })
    }
  }
})
export default router
