<template>
  <div>
    <van-nav-bar fixed title="分类管理" left-arrow @click-left="$router.go(-1)" class="bar" />
    <div v-show="status === false" class="bigdiv" v-for="item in results" :key="item.id">
      <div class="categroy">{{item.name}}（共有{{ item.number }}件）</div>
      <button v-if="item.status === 0" class="del" @click="stop(item.id)">停用该分类</button>
      <button v-else class="del" @click="start(item.id)">启用该分类</button>
    </div>
    <div v-show="status === true" class="div2">
      <input v-model="name" class="ipt" type="text" placeholder="输入分类名称">
      <button class="btn" @click="fun()">确认</button>
    </div>
    <div class="addcategroy" @click="status = !status">
      添加分类
    </div>
  </div>
</template>

<script>
import { getCategroy } from '@/api/getcategroy'
import { stopStatus, startStatus, addCategroy } from '@/api/forcategroy'
export default {
  name: 'addCategroy',
  data () {
    return {
      results: [],
      status: false,
      name: ''
    }
  },
  methods: {
    async getCategroy () {
      const res = await getCategroy()
      this.results = res.results
      console.log(res)
    },
    async stop (id) {
      const res = await stopStatus(id)
      console.log(res)
      this.getCategroy()
    },
    async start (id) {
      const res = await startStatus(id)
      console.log(res)
      this.getCategroy()
    },
    async fun () {
      const res = await addCategroy(this.name)
      console.log(res)
      this.$toast(res.message)
      this.status = !this.status
    }
  },
  created () {
    this.getCategroy()
  }
}
</script>

<style scoped>
    .categroy{
       height: 50px;
       width: 60%;
       border: 1px solid rgb(249, 131, 131);
       float: left;
       line-height: 50px;
    }
    .bigdiv{
        height: 50px;
        border: 1px solid blanchedalmond;
        padding-top: 50px;
        text-align: center;
        margin-bottom: 4px;
    }
    .del{
        width: 20%;
        background-color: antiquewhite;
    }
    .addcategroy{
        width: 100px;
        height: 100px;
        line-height: 100px;
        text-align: center;
        position: fixed;
        left: 40%;
        bottom: 10px;
        border-radius: 50%;
        background-color: rgb(62, 174, 248);
    }
    .ipt{
        width: 100%;
        height: 50px;
        margin-top: 100px;
    }
    .btn{
        width: 100px;
        height: 50px;
        text-align: center;
        line-height: 50px;
    }
    .div2{
        text-align: center;
    }
</style>
