<template>
  <div>
    <van-nav-bar fixed title="发布新闻" left-arrow @click-left="$router.go(-1)" class="bar" />
    <input type="text" v-model="title" placeholder="请输入少于55字的题目">
    <textarea v-model="value" placeholder="请在每段话后面输入br两个字"></textarea>
    <div class="div"><button @click="submit">发布</button></div>
  </div>
</template>

<script>
import { submitNews } from '@/api/submitnew'

export default {
  name: 'submitNewPage',
  data () {
    return {
      value: '',
      title: ''
    }
  },
  methods: {
    async submit () {
      const res = await submitNews(this.value, this.title)
      this.value = ''
      this.title = ''
      this.$toast(res.message)
    }
  }
}
</script>

<style scoped>
.div{
  text-align: center;
}
input{
  width: 100%;
  margin-top: 50px;
}
textarea{
    width: 100%;
    height:400px
}
button{
  background-color: rgb(247, 76, 76);
  width: 100px;
  height: 40px;
}
</style>
