<template>
    <div class="prodetail">
        <van-nav-bar fixed title="商品详情页" left-arrow @click-left="$router.go(-1)" />

        <van-swipe :autoplay="3000" @change="onChange">
            <van-swipe-item v-for="(item, index) in images" :key="index">
                <img :src="item" />
            </van-swipe-item>

            <template #indicator>
                <div class="custom-indicator">{{ current + 1 }} / {{ images.length }}</div>
            </template>
        </van-swipe>

        <!-- 商品说明 -->
        <div class="info">
            <div class="title">
                <div class="price">
                    <span class="oldprice">￥{{ price }}</span>
                    <span class="title">{{ title }}</span>
                </div>
                <div class="sellcount">已售{{ this.buynow_num }}件</div>
            </div>
            <div class="msg">
                {{ detail }}
            </div>
            <div class="haoma">{{ result[0].user_id}}号用户发布，发布类别:{{categroyName }}</div>
            <div class="fenghao">请根据实际情况进行封号处理</div>
            <div class="fenghao2" @click="fenghao">封号</div>
        </div>
        <van-dialog v-model="show" title="弹出框" show-cancel-button :beforeClose="chargeBtn">
            <!--输入框-->
            <van-field v-model="message" rows="1" autosize label="说明原因" type="textarea" placeholder="请输入留言" />
        </van-dialog>
        <!-- 底部 -->
        <div class="footer">
            <div class="btn-buy" @click="comfirm">确定通过</div>
            <van-button type="default" class="no-btn" @click="onClick">不通过</van-button>
        </div>
    </div>
</template>

<script>
import { getGoodMsgById, changeStatus, notComfirm } from '@/api/getgoodsmsg'
import { postFenghao } from '@/api/fenghao.js'
import { getCategroyName } from '@/api/getcategroy'
export default {
  name: 'ProDetail',
  data () {
    return {
      images: [
      ],
      current: 0,
      result: [],
      detail: '',
      price: '',
      mode: 'cart',
      showPannel: false,
      addCount: 1,
      goods_num: 1,
      buynow_num: 0,
      number: 0,
      show: false,
      message: '',
      title: '',
      categroyName: ''

    }
  },
  created () {
    this.getDetail()
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    }
  },
  methods: {
    onChange (index) {
      this.current = index
    },
    async getDetail () {
      const res = await getGoodMsgById(this.goodsId)
      this.result = res.message
      res.message.buyNum = this.addCount
      console.log(this.result)
      this.title = this.result[0].goods_name
      console.log(this.title)
      this.detail = this.result[0].describe
      this.images = this.result[0].pic_url
      this.price = this.result[0].price
      this.goods_num = this.result[0].goods_num
      this.buynow_num = this.result[0].buynow_num
      if (res.status === 0) {
        this.getCatelogname()
      }
    },
    async getCatelogname () {
      const res = await getCategroyName(this.result[0].catelog_id)
      this.categroyName = res.results[0].name
      console.log(res)
    },
    async comfirm () {
      const res = await changeStatus(this.goodsId)
      this.$router.replace({
        path: '/comfirm',
        query: {
          message: res.message
        }
      })
    },
    async fenghao () {
      console.log('start')
      const res = await postFenghao(this.result[0].user_id)
      console.log(res)
      if (res.status === 0) {
        this.$toast(res.message)
      }
    },
    onClick () {
      // 当点击按钮时，显示弹出框
      this.show = true
    },
    async chargeBtn (action, done) { // 确认or取消
      if (action === 'cancel') { // 取消按钮
        done()
      } else if (action === 'confirm') { // 确定按钮
        // 向后端传值并关闭dialog弹出框
        this.show = false
        const res = await notComfirm(this.goodsId, this.message)
        console.log(res)
        if (res.status === 0) {
          this.$router.replace({
            path: '/comfirm',
            query: {
              message: res.message
            }
          })
        }
      };
      this.message = ''
      done()
    }
  }
}
</script>

<style lang="less" scoped>
.prodetail {
    padding-top: 46px;

    ::v-deep .van-icon-arrow-left {
        color: #333;
    }

    img {
        display: block;
        width: 100%;
    }
    .title{
        display: inline-block;
        margin: auto;
    }
    .custom-indicator {
        position: absolute;
        right: 10px;
        bottom: 10px;
        padding: 5px 10px;
        font-size: 12px;
        background: rgba(0, 0, 0, 0.1);
        border-radius: 15px;
    }

    .desc {
        width: 100%;
        overflow: scroll;

        ::v-deep img {
            display: block;
            width: 100% !important;
        }
    }
    .fenghao{
        text-align: center;
    }
    .fenghao2{
            text-align:center;
            margin: auto;
            height: 36px;
            line-height: 36px;
            width: 120px;
            border-radius: 18px;
            background-color: #fe5630;
    }
    .info {
        padding: 10px;
    }

    .title {
        display: flex;
        justify-content: space-between;

        .oldprice {
            color: #fa2209;
            font-size: 16px;
            margin-left: 5px;
        }

        .sellcount {
            color: #f7f6f6;
            font-size: 16px;
            position: relative;
            top: 4px;
        }
    }

    .msg {
        font-size: 16px;
        line-height: 24px;
        margin-top: 5px;
        word-break: break-all;
    }

    .service {
        display: flex;
        justify-content: space-between;
        line-height: 40px;
        margin-top: 10px;
        font-size: 16px;
        background-color: #fafafa;

        .left-words {
            span {
                margin-right: 10px;
            }

            .van-icon {
                margin-right: 4px;
                color: #fa2209;
            }
        }
    }

    .comment {
        padding: 10px;
    }

    .comment-title {
        display: flex;
        justify-content: space-between;

        .right {
            color: #f7f0f0;
        }
    }

    .comment-item {
        font-size: 16px;
        line-height: 30px;

        .top {
            height: 30px;
            display: flex;
            align-items: center;
            margin-top: 20px;

            img {
                width: 20px;
                height: 20px;
            }

            .name {
                margin: 0 10px;
            }
        }

        .time {
            color: #ebe5e5;
        }
    }

    .footer {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 55px;
        background-color: #fff;
        border-top: 1px solid #ccc;
        display: flex;
        justify-content: space-evenly;
        align-items: center;

        .icon-home,
        .icon-cart {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            font-size: 14px;

            .van-icon {
                font-size: 24px;
            }
        }
        .no-btn,
        .btn-add,
        .btn-buy {
            height: 36px;
            line-height: 36px;
            width: 120px;
            border-radius: 18px;
            background-color: #ffa900;
            text-align: center;
            color: #fff;
            font-size: 14px;
        }

        .btn-buy {
            background-color: #fe5630;
        }
        .no-btn{
            background-color:#b2b0b0
        }
    }
}

.product {
    .product-title {
        display: flex;

        .left {
            img {
                width: 90px;
                height: 90px;
            }

            margin: 10px;
        }

        .right {
            flex: 1;
            padding: 10px;

            .price {
                font-size: 14px;
                color: #fe560a;

                .nowprice {
                    font-size: 24px;
                    margin: 0 5px;
                }
            }
        }
    }

    .num-box {
        display: flex;
        justify-content: space-between;
        padding: 10px;
        align-items: center;
    }

    .btn,
    .btn-none {
        height: 40px;
        line-height: 40px;
        margin: 20px;
        border-radius: 20px;
        text-align: center;
        color: rgb(255, 255, 255);
        background-color: rgb(255, 148, 2);
    }

    .btn.now {
        background-color: #fe5630;
    }

    .btn-none {
        background-color: #cccccc;
    }
}

.footer .icon-cart {
    position: relative;
    padding: 0 6px;

    .num {
        z-index: 999;
        position: absolute;
        top: -2px;
        right: 0;
        min-width: 16px;
        padding: 0 4px;
        color: #fff;
        text-align: center;
        background-color: #ee0a24;
        border-radius: 50%;
    }
}

.tips {
    padding: 10px;
}
</style>
