<template>
  <div>
    <van-nav-bar title="注册" left-text="返回" left-arrow @click-left="$router.go(-1)" />
    <div class="main">
      <div class="username"><input v-model="username" type="text" placeholder="请输入账号"></div>
      <div class="password"><input v-model="firstpwd" type="password" placeholder="请输入密码"></div>
      <div class="username"><input v-model="mbname" type="text" placeholder="请输入密保名称"></div>
      <div class="username"><input v-model="mbword" type="text" placeholder="请输入密保内容"></div>
    </div>
    <div class="booter">
      <input type="button" value="注册" @click="fun">
    </div>
  </div>
</template>

<script>
import { zhuce } from '@/api/zhuce'
import { login } from '@/api/login'
export default {
  name: 'zhuceIndex',
  data () {
    return {
      username: '',
      firstpwd: '',
      secondpwd: '',
      mbname: '',
      mbword: '',
      value: 'user'
    }
  },
  methods: {
    async fun () {
      if (this.username.length < 6 || this.username.length > 18) {
        this.$toast('账号长不能小于6或大于18')
        return
      }
      if (this.firstpwd.length < 6 || this.firstpwd.length > 18) {
        this.$toast('密码不能小于6或大于18')
        return
      }
      if (this.mbname.length < 1 || this.mbword.length < 1) {
        this.$toast('密保不能为空')
        return
      }
      const res = await zhuce(this.username, this.firstpwd, this.mbword, this.mbname)
      console.log(res)
      if (res.status === 0) {
        console.log(1)
        const res2 = await login(this.username, this.firstpwd, this.value)
        console.log(res2)
        const obj = { token: res2.token, userId: '', role: res2.role }
        this.$store.commit('getUserInfo', obj)
        if (res2.status === 0) {
          setTimeout(() => { this.$toast('登录成功') }, 1000)
        }
        this.$router.replace('/')
      }
    }
  }

}
</script>

<style scoped>

.main {
    height: 250px;
    width: 250px;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 20px;
}

.username {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 20px;
    background-color: white;
}

.username input {
    width: 100%;
    height: 100%;
}

.password {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 20px;
    background-color: white;
}

.password input {
    width: 100%;
    height: 100%;
}

.booter {
    height: 100px;
    width: 250px;
    margin: auto;
}

.booter input {
    height: 50px;
    width: 150px;
    margin-left: 50px;
    background-color: rgb(233, 122, 146);
    color: #fff;
}</style>
