import { getCart } from '@/api/getcart'
import { changeCart, delSelect } from '@/api/postcart'
import { Toast } from 'vant'

export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    setCartList (state, newList) {
      state.cartList = newList
    },
    toggleCheck (state, goodsId) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.isChecked = !goods.isChecked
    },
    toggleAllCheck (state, flag) {
      state.cartList.forEach(item => {
        item.isChecked = flag
      })
    },
    changeCount (state, { Id, value }) {
      const obj = state.cartList.find(item => item.id === Id)
      obj.num = value
    }
  },
  actions: {
    // 获取购物车数据
    async getCartAction (context) {
      let { results } = await getCart()
      if (results) {
        results = results.filter((item) => {
          return item.details !== undefined
        })
        console.log(results)
        if (results) {
          results.forEach(item => {
            item.isChecked = true
          })
        }
        console.log(results)
        context.commit('setCartList', results)
      }
    },
    // 更新购物车数据
    async changeCountAction (context, obj) {
      const { value, Id } = obj
      context.commit('changeCount', {
        value,
        Id
      })
      await changeCart(value, Id)
    },
    // 删除购物车数据
    async delSelect (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(item => item.id)
      await delSelect(cartIds)
      Toast('删除成功')

      // 重新拉取最新的购物车数据 (重新渲染)
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 购物车总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item, index) => sum + item.num, 0)
    },
    // 选出打钩的商品
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 选择的商品总数
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => sum + item.num, 0)
    },
    // 选择的商品总价格
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item, index) => {
        return sum + item.num * item.details[0].price
      }, 0).toFixed(2)
    },
    // 是否全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    }
  }
}
