<template>
  <div class="prodetail">
    <van-nav-bar fixed title="商品详情页" left-arrow @click-left="$router.go(-1)" />

    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="(item,index) in images" :key="index">
        <img :src="item" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }} / {{ images.length }}</div>
      </template>
    </van-swipe>

    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="oldprice">￥{{ price }}</span>
        </div>
        <div class="sellcount">已售{{ this.buynow_num}}件</div>
      </div>
      <div class="msg">
        {{ detail }}
      </div>

      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />送货上门</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <!-- 底部 -->
    <div class="footer">
      <div class="icon-home"  @click="$router.push('/tuijian')">
        <van-icon name="wap-home-o" />
        <span>首页</span>
      </div>
      <div class="icon-cart" @click="$router.push('/cart')">
        <van-icon name="shopping-cart-o" />
        <span>购物车</span>
     </div>
      <div class="btn-add" @click="goCart">加入购物车</div>
      <div class="btn-buy" @click="goBuy">立刻购买</div>
    </div>
    <!--弹窗-->
    <van-action-sheet v-model="showPannel" :title="mode === 'cart'?'加入购物车':'立刻购买'">
      <div class="product">
      <div class="product-title">
        <div class="left">
          <img :src="images[0]" alt="">
        </div>
        <div class="right">
          <div class="price">
            <span>¥</span>
            <span class="nowprice">{{ this.price }}</span>
          </div>
          <div class="count">
            <span>库存</span>
            <span>{{ this.goods_num }}</span>
          </div>
        </div>
      </div>
      <div class="num-box">
        <span>数量</span>
        <CountBox v-model="addCount" :goodsNum="goods_num"></CountBox>
      </div>
      <div class="showbtn">
        <div class="btn" v-if="mode === 'cart'" @click="addCart()">加入购物车</div>
        <div class="btn now" v-else @click="goBuyNow">立刻购买</div>
      </div>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { getGoodMsgById } from '@/api/getgoodsmsg'
import CountBox from '@/components/CountBox.vue'
import store from '@/store'
import { postCart } from '@/api/postcart'

export default {
  name: 'ProDetail',
  components: {
    CountBox
  },
  data () {
    return {
      images: [
      ],
      current: 0,
      result: [],
      detail: '',
      price: '',
      mode: 'cart',
      showPannel: false,
      addCount: 1,
      goods_num: 1,
      buynow_num: 0,
      number: 0
    }
  },
  created () {
    this.getDetail()
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    }
  },
  methods: {
    onChange (index) {
      this.current = index
    },
    goBuyNow () {
      if (!store.getters.token) {
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
      }

      this.$router.push(
        {
          path: '/pay',
          query: {
            mode: 'buynow',
            num: this.addCount,
            Id: this.goodsId
            // cartIds: this.selCartList.map(item => item.id).join(',')
          }
        })
    },
    goBuy () {
      this.mode = 'buy'
      this.showPannel = true
    },
    goCart () {
      this.mode = 'cart'
      this.showPannel = true
    },
    async addCart () {
      if (!store.getters.token) {
        console.log(1)
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要先登录才能继续操作哦',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => { })
        return
      }
      if (this.addCount > this.goods_num) {
        console.log(this.addCount, this.goods_num)
        this.addCount = this.goods_num
        console.log(this.addCount, this.goods_num)
        this.$toast('商品数量不足已为您调整为最大数量')
      }
      const res = await postCart(this.goodsId, this.addCount, this.goods_num)
      console.log(res)
      this.number = res.results
      if (res.message) {
        this.$toast(res.message)
      }
      this.showPannel = false
      console.log('进行加入购物车操作')
    },
    async getDetail () {
      const { message } = await getGoodMsgById(this.goodsId)
      this.result = message
      message.buyNum = this.addCount
      console.log(this.result[0].describe)
      this.detail = this.result[0].describe
      this.images = this.result[0].pic_url
      this.price = this.result[0].price
      this.goods_num = this.result[0].goods_num
      this.buynow_num = this.result[0].buynow_num
    }
  }
}
</script>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;

  ::v-deep .van-icon-arrow-left {
    color: #333;
  }

  img {
    display: block;
    width: 100%;
  }

  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }

  .desc {
    width: 100%;
    overflow: scroll;

    ::v-deep img {
      display: block;
      width: 100% !important;
    }
  }

  .info {
    padding: 10px;
  }

  .title {
    display: flex;
    justify-content: space-between;

    .oldprice {
      color: #fa2209;
      font-size: 16px;
      margin-left: 5px;
    }

    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }

  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
    word-wrap: break-word;
  }

  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;

    .left-words {
      span {
        margin-right: 10px;
      }

      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }

  .comment-title {
    display: flex;
    justify-content: space-between;

    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;

    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;

      img {
        width: 20px;
        height: 20px;
      }

      .name {
        margin: 0 10px;
      }
    }

    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;

    .icon-home,
    .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;

      .van-icon {
        font-size: 24px;
      }
    }

    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }

    .btn-buy {
      background-color: #fe5630;
    }
  }
}
.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn, .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}
.footer .icon-cart {
  position: relative;
  padding: 0 6px;
  .num {
    z-index: 999;
    position: absolute;
    top: -2px;
    right: 0;
    min-width: 16px;
    padding: 0 4px;
    color: #fff;
    text-align: center;
    background-color: #ee0a24;
    border-radius: 50%;
  }
}
.tips {
  padding: 10px;
}</style>
