<template>
  <div class="pay">
    <van-nav-bar fixed title="订单结算台" left-arrow @click-left="$router.go(-1)" />

    <!-- 地址相关 -->
    <div class="address" @click="$router.push('/updatauserinfo')">

      <div class="left-icon">
        <van-icon name="logistics" />
      </div>

      <div class="info" v-if="true">
        <div class="info-content">
          <span class="name">{{ nickname }}</span>
          <span class="mobile">{{ telNum }}</span>
        </div>
        <div class="info-address">
          {{ address }}
        </div>
      </div>

      <div class="right-icon">
        <van-icon name="arrow" />
      </div>
    </div>

    <!-- 订单明细 -->
    <div class="pay-list">
      <div class="list" v-for="item in results" :key="item.id">
        <div class="goods-item">
          <div class="left">
            <img :src=item.pic_url alt="" />
          </div>
          <div class="right">
            <p class="tit">
              {{ item.describe }}
            </p>
            <p class="info">
              <span class="count">x{{ item.num }}</span>
              <span class="price">¥{{ item.oneTotalPrice }}</span>
            </p>
          </div>
        </div>
      </div>

      <div class="flow-num-box">
        <span>共 {{ num }} 件商品，合计：</span>
        <span class="money" v-if="results[0]">￥{{ results[0].totalPrice }}</span>
      </div>

      <div class="pay-detail">
        <div class="pay-cell">
          <span>订单总金额：</span>
          <span class="red" v-if="results[0]">￥{{results[0].totalPrice }}</span>
        </div>

        <div class="pay-cell">
          <span>配送费用：</span>
          <span class="red">+￥0.00</span>
        </div>
      </div>

      <!-- 支付方式 -->
      <div class="pay-way">
        <div class="pay-cell">
          <span><van-icon name="balance-o" />余额支付（可用 ¥ {{ remainingSum }} 元）</span>
          <!-- <span>请先选择配送地址</span> -->
          <span class="red"><van-icon name="passed" /></span>
        </div>
      </div>

      <!-- 买家留言 -->
      <div class="buytips">
        <textarea v-model="remark" placeholder="选填：买家留言（50字内）" name="" id="" cols="30" rows="10"></textarea>
      </div>
    </div>

    <!-- 底部提交 -->
    <div class="footer-fixed">
      <div class="left">实付款：<span v-if="results[0]">{{ results[0].totalPrice }}</span></div>
      <div class="tipsbtn" @click="goPay">提交订单</div>
    </div>
  </div>
</template>

<script>
import { getUserInfo } from '@/api/getuserinfo'
import { checkOrder, payMent } from '@/api/buygoods'
import { queryid } from '@/api/getorderid'

export default {
  name: 'PayIndex',
  data () {
    return {
      remainingSum: 0,
      remark: '',
      goodsMsg: [],
      results: [],
      telNum: '',
      address: '',
      nickname: '',
      id: {},
      obj: {}
    }
  },
  methods: {
    async goPay () {
      if (this.mode === 'cart') {
        this.obj = { mode: this.mode, remark: this.remark, cartIds: this.cartIds, nickname: this.nickname, address: this.address, tel_Num: this.telNum }
        this.$store.commit('setOrderInfo', this.obj)
        const res3 = await payMent(this.id, this.results[0].totalPrice)
        window.location.href = res3.url
        // const res = await payOrder(this.mode, { remark: this.remark, cartIds: this.cartIds, nickname: this.nickname, address: this.address, tel_Num: this.telNum })
        // console.log(res)
      }
      if (this.mode === 'buynow') {
        this.obj = { mode: this.mode, remark: this.remark, goodsId: this.goodsId, num: this.num, nickname: this.nickname, address: this.address, tel_Num: this.telNum }
        // await payOrder(this.mode, { remark: this.remark, goodsId: this.goodsId, num: this.buyNum, nickname: this.nickname, address: this.address, tel_Num: this.telNum })
        this.$store.commit('setOrderInfo', this.obj)
        const res2 = await payMent(this.id, this.results[0].totalPrice)
        window.location.href = res2.url
      }
    }
  },
  computed: {
    goodsId () {
      return this.$route.query.Id
    },
    num () {
      return this.$route.query.num
    },
    mode () {
      return this.$route.query.mode
    },
    cartIds () {
      return this.$route.query.cartIds
    }
  },
  destroyed () {
    this.$store.commit('setOrderInfo', this.obj)
  },
  async created () {
    const res = await getUserInfo()
    console.log('ceshi1')
    console.log(res)
    this.telNum = res.results.tel_Num
    this.address = res.results.address
    this.nickname = res.results.nickname || '小绿'
    this.remainingSum = res.results.purse
    if (this.mode === 'buynow') {
      const { results } = await checkOrder(this.mode, { goodsId: this.goodsId, buyNum: this.num })
      this.results = results
    }
    if (this.mode === 'cart') {
      const { results } = await checkOrder(this.mode, { cartIds: this.cartIds })
      console.log(results)
      this.results = results
      console.log(this.results[0].totalPrice)
    }
    console.log(this.results)
    const result = await queryid()
    this.id = result.id + 49
    this.id = 'gan_feng_lin_bi_se_123' + this.id
  }
}
</script>

<style lang="less" scoped>
.pay {
  padding-top: 46px;
  padding-bottom: 46px;
  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}
.address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  font-size: 14px;
  color: #666;
  position: relative;
  background: url(@/assets/border-line.png) bottom repeat-x;
  background-size: 60px auto;
  .left-icon {
    margin-right: 20px;
  }
  .right-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-7px);
  }
}
.goods-item {
  height: 100px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;
  .left {
    width: 100px;
    img {
      display: block;
      width: 80px;
      margin: 10px auto;
    }
  }
  .right {
    overflow: hidden;
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;
    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;
      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px;
  font-size: 14px;
  border-bottom: 1px solid #efefef;
  .money {
    color: #fa2209;
  }
}

.pay-cell {
  font-size: 14px;
  padding: 10px 12px;
  color: #333;
  display: flex;
  justify-content: space-between;
  .red {
    color: #fa2209;
  }
}
.pay-detail {
  border-bottom: 1px solid #efefef;
}

.pay-way {
  font-size: 14px;
  padding: 10px 12px;
  border-bottom: 1px solid #efefef;
  color: #333;
  .tit {
    line-height: 30px;
  }
  .pay-cell {
    padding: 10px 0;
  }
  .van-icon {
    font-size: 20px;
    margin-right: 5px;
  }
}

.buytips {
  display: block;
  textarea {
    display: block;
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer-fixed {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  border-top: 1px solid #efefef;
  font-size: 14px;
  display: flex;
  .left {
    flex: 1;
    padding-left: 12px;
    color: #666;
    span {
      color:#fa2209;
    }
  }
  .tipsbtn {
    width: 121px;
    background: linear-gradient(90deg,#f9211c,#ff6335);
    color: #fff;
    text-align: center;
    line-height: 46px;
    display: block;
    font-size: 14px;
  }
}
</style>
