<template>
  <div class="all">
    <van-nav-bar fixed title="个人中心" left-arrow @click-left="$router.go(-1)" />
    <div class="div" v-show="role === 0">余额<span>￥</span>{{ userInfo.purse|| '0'}}</div>
    <div class="div" v-show="token && role === 0" @click="fun">修改个人信息</div>
    <div class="div" v-show="token && role === 0" @click="$router.push('/changepwd')">修改账户密码</div>
    <div class="div" @click="$router.push('/login')">登录</div>
  </div>
</template>

<script>
import { getUserInfo } from '@/api/getuserinfo'
import store from '@/store'

export default {
  name: 'UserMsg',
  data () {
    return {
      userInfo: {},
      token: store.getters.token,
      role: store.getters.role
    }
  },
  async created () {
    if (this.token) {
      const { data } = await getUserInfo()
      console.log(data)
      this.userInfo = data
    }
  },
  methods: {
    fun () {
      const parObj = JSON.stringify(this.userInfo)
      // 路由跳转
      this.$router.push({
        path: '/updatauserinfo',
        query: {
          obj: parObj
        }
      })
    }
  }
}
</script>

<style scoped>
span{
    color: red;
}
.div{
    height: 60px;
    width: 100%;
    background-color: antiquewhite;
    margin-bottom: 50px;
    line-height: 60px;
    text-align: center;
}
.all{
  padding-top: 46px;
}

</style>
