import request from '@/utils/request'
import store from '@/store'

export const getGoodsMsg = function () {
  return request.get('/api/getgoodsmsg',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
export const getGoodsMsgNotComfirm = function () {
  return request.get('/admin/getgoodsmsgnotcomfirm',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}

export const getPersonGoods = function () {
  return request.get('/my/getpersongoods',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
export const changeStatus = function (id) {
  return request.post('/admin/changestatus', {
    id
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}

export const notComfirm = function (id, whynot) {
  return request.post('/admin/notcomfirm', {
    id,
    whynot
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}

export const getGoodMsgById = function (id) {
  return request.get('/api/getgoodsmsgbyid', {
    params: {
      id
    }
  })
}
