<template>
  <div class="category">
    <!-- 分类 -->
    <van-nav-bar title="全部分类" fixed />

    <!-- 搜索框 -->
    <van-search readonly shape="round" background="#f1f1f2" placeholder="请输入搜索关键词" @click="$router.push('/search')" />

    <!-- 分类列表 -->
    <div>
      <div  v-for="item in list" :key="item.id" @click="$router.push(`/list?Search=${item.name}`)" class="smallDiv">{{ item.name }}</div>
    </div>
  </div>
</template>

<script>
import { getCategroyData } from '@/api/categroy'
export default {
  name: 'CategoryPage',
  created () {
    this.getCategoryList()
  },
  data () {
    return {
      list: []
    }
  },
  methods: {
    async getCategoryList () {
      const { results } = await getCategroyData()
      this.list = results
    }
  }
}
</script>

<style lang="less" scoped>
// 主题 padding
.category {
  padding-top: 100px;
  padding-bottom: 50px;
  height: 100vh;
}
.smallDiv{
  height: 40px;
  width: 140px;
  padding: 10px;
  margin: 20px;
  float: left;
  text-align: center;
  background-color: #f7f5f5;
}
// 导航条样式定制
.van-nav-bar {
  z-index: 999;
}

// 搜索框样式定制
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}</style>
