                {{ item.describe || '没有该商品的描述'}}
            </div>
            <div class="goods-trade">
                <p>¥ {{ item.price || '0' }}</p>
                <p>x {{ item.num || '1'}}</p>
            </div>
            <div v-if="active === 0">
                <div v-show="item.sell_order_status === 0 && item.buy_order_status === 1 " class="fahuo">正在发货</div>
                <div v-if="item.buy_order_status === 1 " class="button1" @click="comfirm(item.id)">确定收货</div>
                <div v-else class="button2">已确定收货</div>
            </div>
            <div v-if=" active===1" class="button">
                <div v-if="item.sell_order_status === 1" class="button1" @click="comfirm(item.id)">确定发货</div>
                <div v-else class="button2">已确定发货</div>
            </div>
            <div v-if="active === 2" class="mark">
                <div v-if="item.status === 0">审核通过</div>
                <div v-if="item.status === 1">未审核</div>
                <div v-if="item.status === 2" class="red">未通过
                    <div>未通过原因:{{ item.whynot }}</div>
                </div>

            </div>
        </div>
        <div class="words" v-show="item.words">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;留言：{{ item.words }}
        </div>
        <div class="address" v-show="item.nickname">
