                    item.address || '6栋驿站'}}</span></div>
        </div>
    </div>
</template>

<script>
import { comfirmStatus } from '@/api/comfirm.js'
export default {
  props: {
    item: {
      type: Object,
      default: () => {
        return {}
      }
    },
    active: {
      type: Number
    }
  },
  methods: {
    async comfirm (id) {
      console.log(this.item)
      await comfirmStatus(id, this.active)
      this.$router.go(0)
    //   this.$emit('getId', { msg: '重新渲染页面' })
    }
  }
}
</script>

