<template>
    <div>
        <van-nav-bar title="修改个人信息" left-text="返回" left-arrow @click-left="$router.go(-1)" />
        <div class="main">
            <div class="nickname"><input v-model="nickname" type="text" placeholder="请输入昵称"></div>
            <div class="tel_num"><input v-model="tel_num" type="text" placeholder="请输电话"></div>
            <div class="address"><input v-model="address" type="text" placeholder="请输入收货地址"></div>
        </div>
        <div class="booter">
            <input type="button" value="确定修改" @click="fun">
        </div>
    </div>
</template>

<script>
import { updataUserinfo } from '@/api/updatauserinfo'
export default {
  name: 'updataUserinfo',
  data () {
    return {
      data: {},
      nickname: '',
      tel_num: '',
      address: ''
    }
  },
  methods: {
    async fun () {
      const res = await updataUserinfo(this.nickname, this.tel_num, this.address)
      this.$toast(res.message)
      this.$router.go(-1)
    }
  },
  created () {
    this.data = JSON.parse(this.$route.query.obj)
    console.log(this.data)
    this.nickname = this.data.nickname
    this.tel_num = this.data.tel_num
    this.address = this.data.address
  }

}
</script>

<style>
.main {
    margin-top: 50px;
    height: 250px;
    width: 250px;
    margin: auto;
    margin: 50px;
}

.nickname {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 50px;
    background-color: white;
}

.nickname input {
    width: 100%;
    height: 100%;
}

.tel_num {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 50px;
    background-color: white;
}

.tel_num input {
    width: 100%;
    height: 100%;
}

.address {
    width: 250px;
    height: 50px;
    margin: auto;
    margin-bottom: 50px;
    background-color: white;
}

.address input {
    width: 100%;
    height: 100%;
}

.booter {
    height: 100px;
    width: 250px;
    margin: auto;
}

</style>
