import request from '@/utils/request'
import store from '@/store/index'

export const stopStatus = function (id) {
  return request.post('/admin/stopstatus', {
    id
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}

export const startStatus = function (id) {
  return request.post('/admin/startstatus', {
    id
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}
export const addCategroy = function (name) {
  return request.post('/admin/addcategroy', {
    name
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}
