import request from '@/utils/request'
import store from '@/store'

export const getSelect = function () {
  return request.get('/my/getcategroy',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}

export const getCategroy = function () {
  return request.get('/my/getcategroy2',
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}

// 根据分类id获取分类名称

export const getCategroyName = function (id) {
  return request.get('/my/getcategroyname',
    {
      params: {
        id
      },
      headers: {
        Authorization: store.getters.token
      }
    })
}
