import request from '@/utils/request'
import store from '@/store/index'

export const change = function (username, oldpwd, newpwd, mbword) {
  return request.post('/my/updatepwd',
    {
      username,
      oldpwd,
      newpwd,
      mbword
    },
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
