<template>
  <div>
    <van-nav-bar fixed title="审核商品" left-arrow @click-left="$router.go(-1)" class="bar" />
    <div class="all">
      <div>
        <GoodsItem :goods="goodsMsg"></GoodsItem>
      </div>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/NotComfirmGoodsMsg.vue'
import { getGoodsMsgNotComfirm } from '@/api/getgoodsmsg'
import { Toast } from 'vant'

export default {
  name: 'comfirmPage',
  components: {
    GoodsItem
  },
  data () {
    return {
      goodsMsg: [],
      msg: ''
    }
  },
  async created () {
    const { results } = await getGoodsMsgNotComfirm()
    this.goodsMsg = results || {}
    if (this.$route.query.message) {
      console.log(1, 1, this.$route.query.message)
      this.msg = this.$route.query.message
      Toast(this.msg)
    }
  }
}
</script>

<style scoped>
.all{
    padding-top: 30px;
}
</style>
