<template>
  <div class="order">
    <div class="user" @click="$router.push('/usermsg')">进入个人中心</div>
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="active">
      <van-tab title="买货"></van-tab>
      <van-tab title="卖货"></van-tab>
      <van-tab title="发布的商品"></van-tab>
    </van-tabs>
    <!-- @getId="changeById" -->
    <OrderListItem v-for="item in results" :key="item.id" :item="item" :active="active"></OrderListItem>
  </div>
</template>

<script>
import OrderListItem from '@/components/OrderListItem.vue'
import { getOrderMsg } from '@/api/getordermsg'
import { getPersonGoods } from '@/api/getgoodsmsg'
import store from '@/store'
export default {
  name: 'UserPage',
  components: {
    OrderListItem
  },
  methods: {
    async getOrderList () {
      if (store.getters.token) {
        const { results } = await getOrderMsg(this.active)
        if (results) {
          for (let i = 0; i < results.length; i++) {
            if (results[i].pic_url) {
              results[i].pic_url = results[i].pic_url.split(',')
            }
          }
        }
        this.results = results
      }
    },
    async getOrderList2 () {
      if (store.getters.token) {
        const { results } = await getPersonGoods()
        console.log(results)
        this.results = results
      }
    }
  },
  created () {
    this.getOrderList()
  },
  data () {
    return {
      active: 0,
      results: []
    }
  },
  watch: {
    active: {
      immediate: true,
      handler () {
        if (this.active === 0 || this.active === 1) {
          this.getOrderList()
        } else if (this.active === 2) {
          this.getOrderList2()
        }
      }
    }
  }
}
</script>
<style lang="less" scoped>

.order {
  background-color: #fafafa;
}
.user{
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background-color: rgb(249, 243, 235);
}
.van-tabs {
  position: sticky;
  top: 0;
}
</style>
