import request from '@/utils/request'
import store from '@/store'

export const postGoodsInfo = (describe, filesImgUrl, categroy, price, categroyId, title) => {
  return request.post('/my/postgoodsinfo', {
    describe,
    filesImgUrl,
    categroy,
    price,
    categroyId,
    title
  },
  {
    headers: {
      Authorization: store.getters.token
    }
  })
}
