import request from '@/utils/request'
import store from '@/store/index'

export const checkOrder = (mode, obj) => {
  return request.get('/my/getorder',
    {
      params: {
        mode,
        ...obj
      },
      headers: {
        Authorization: store.getters.token
      }
    }
  )
}
export const payOrder = (obj) => {
  return request.get('/my/payorder',
    {
      params: {
        ...obj
      },
      headers: {
        Authorization: store.getters.token
      }
    }
  )
}
export const payMent = (id, totalprice) => {
  return request.post('/api/zhifubao',
    {
      id,
      totalprice: totalprice.toFixed(2)

    },
    {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }
  )
}
export const queryOrder = (id, id2) => {
  return request.post('/api/queryOrder',
    {
      out_trade_no: id,
      trade_no: id2
    },
    {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }
  )
}
