const INFO_KEY = 'hm_shopping_info'
const HM_HISTORY_KEY = 'hm_history_info'
const ORDER_KEY = 'ganfenglin'

// 获取个人信息
export const getInfo = () => {
  const result = localStorage.getItem(INFO_KEY)
  return result
    ? JSON.parse(result)
    : {
        token: '',
        userId: ''
      }
}

// 存储个人信息
export const setInfo = function (info) {
  localStorage.setItem(INFO_KEY, JSON.stringify(info))
}

export const removeInfo = function () {
  localStorage.removeItem(INFO_KEY)
}

// 获取搜索记录
export const getSearchInfo = function () {
  const result = localStorage.getItem(HM_HISTORY_KEY)
  return result ? JSON.parse(result) : []
}

// 删除搜索记录
export const setSearchInfo = function (info) {
  localStorage.setItem(HM_HISTORY_KEY, JSON.stringify(info))
}

// 获取将要储存的商品数据
export const getOrder = () => {
  console.log(1)
  const result = localStorage.getItem(ORDER_KEY)
  console.log(result)
  return result
    ? JSON.parse(result)
    : {
        mode: '错误'
      }
}

// 存储将要储存的商品数据
export const setOrder = function (info) {
  console.log(info)
  localStorage.setItem(ORDER_KEY, JSON.stringify(info))
}
