import request from '@/utils/request'
import store from '@/store/index'

export const updataUserinfo = function (nickname, telNum, address) {
  return request.post('/my/updatauserinfo',
    {
      nickname,
      tel_num: telNum,
      address
    },
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
