import request from '@/utils/request'
import store from '@/store/index'

export const postCart = function (goodsId, number, remainingNum) {
  return request.post('/my/postcart',
    {
      goodsId,
      number,
      remainingNum
    },
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}

export const changeCart = function (value, Id) {
  return request.post('/my/changecart',
    {
      value,
      Id
    },
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
export const delSelect = function (Id) {
  return request.post('/my/delcart',
    {
      Id
    },
    {
      headers: {
        Authorization: store.getters.token
      }
    })
}
