<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="active">
      <van-tab title="买货"></van-tab>
      <van-tab title="卖货"></van-tab>
    </van-tabs>

    <OrderListItem v-for="item in results" :key="item.id" :item="item" :active="active"></OrderListItem>
  </div>
</template>

<script>
import OrderListItem from '@/components/OrderListItem.vue'
import { getOrderMsg } from '@/api/getordermsg'
export default {
  name: 'OrderPage',
  components: {
    OrderListItem
  },
  methods: {
    async getOrderList () {
      const { results } = await getOrderMsg(this.active)
      this.results = results
      console.log(results)
      if (results) {
        for (let i = 0; i < results.length; i++) {
          results[i].pic_url = results[i].pic_url.split(',')
        }
        console.log(results)
      }
    }
  },
  data () {
    return {
      active: 0,
      results: []
    }
  },
  watch: {
    active: {
      immediate: true,
      handler () {
        this.getOrderList()
      }
    }
  }
}
</script>
<style lang="less" scoped>
.order {
  background-color: #fafafa;
}

.van-tabs {
  position: sticky;
  top: 0;
}
</style>
